.questions {
    height: 100vh;
    @extend %center;
    overflow: hidden;

    @media screen and (max-width: 720px){
        height: 110vh;
    }
    
    & .questions-container {
        @extend %center;
        color: #222222;
        width: 80%;
        background: #8F8EBB;
        height: 50%;
        border-radius: 25px;

        @media screen and (max-width: 720px){
            height: 580px;
        }
        
        & .current-question {
            padding: 0 100px;

            @media screen and (max-width: 720px){
                height: 100%;
            }

            
            & h2 {
                @extend %center;
                font-size: 1.2rem;
                display: flex;
                justify-content: center;
                margin-bottom: 20px;

                @media screen and (max-width: 720px){
                    margin-bottom: 50px;
                    margin-top: 50px;
                }
            }

            & .current-question-container {
                height: 70px;
                display: flex;

                @media screen and (max-width: 720px){
                    height: 150px;
                }
            }
            
            & h3 {
                @extend %center;
                text-align: center;
                font-size: 1.5rem;
                letter-spacing: 1px;

                // @media screen and (max-width: 720px){
                //     width: 50%;
                // }
            }
            
            & form {
                @extend %center;

                @media screen and (max-width: 720px){
                    display: wrap;
                    flex-wrap: wrap;
                    width: 300px;
                }
                
                & .label {
                    @extend %center;                    
                    width: 110px;
                    margin: 40px 40px 20px 40px;
                    padding: 10px;
                    border-radius: 25px;
                    background: #D9D9D9;
                    cursor: pointer;
                    font-size: 0.8rem;
                    letter-spacing: 1px;

                    @media screen and (max-width: 720px){
                        width: 35%;
                        box-sizing: border-box;
                        margin: 40px 20px 20px 20px;
                    }

                    & input {
                        appearance: none;
                    }
                }
                & label:hover {
                    color: #D9D9D9;
                    background: #222222;
                }
                
                & .label-checked {
                    @extend %center;                    
                    width: 110px;
                    margin: 40px 40px 20px 40px;
                    padding: 10px;
                    border-radius: 25px;
                    color: #D9D9D9;
                    background: #222222;
                    cursor: pointer;
                    font-size: 0.8rem;
                    letter-spacing: 1px;

                    @media screen and (max-width: 720px){
                        width: 35%;
                        box-sizing: border-box;
                        margin: 40px 20px 20px 20px;
                    }
                    
                    & input {
                        appearance: none;
                    }
                }
            }
            
            & button {
                @extend %center;
                width: 160px;
                margin-top: 30px;
                font-size: 1rem;
                padding: 10px;
                border-radius: 25px;
                border: none;
                cursor: pointer;
                background: #D9D9D9;
            }
        }
    }
}